@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
a {
  text-decoration: none;
  color: #000;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list-reset {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.about-us {
  padding: 0 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
}
.about-us__title {
  margin: 0;
  padding: 0;
  margin-top: 37px;
  font-size: 25px;
  line-height: 30px;
}
.about-us__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.about-us__item {
  position: relative;
  margin-top: 83px;
  width: 260px;
  font-size: 17px;
  line-height: 20px;
}
.about-us__item:first-child {
  margin-top: 82px;
}
.about-us__item:nth-child(5) {
  margin-top: 84px;
}
.about-us__item:last-child {
  margin-top: 84px;
}
.about-us__item:first-child::before {
  position: absolute;
  content: "";
  top: -54px;
  left: 8px;
  background-image: url("../../img/about-us-1.svg");
  width: 33px;
  height: 38px;
}
.about-us__item:nth-child(2)::before {
  position: absolute;
  content: "";
  top: -53px;
  left: 0;
  background-image: url("../../img/about-us-2.svg");
  width: 47px;
  height: 27px;
}
.about-us__item:nth-child(3)::before {
  position: absolute;
  content: "";
  top: -48px;
  left: 7px;
  background-image: url("../../img/about-us-3.svg");
  background-repeat: no-repeat;
  width: 34px;
  height: 27px;
}
.about-us__item:nth-child(4)::before {
  position: absolute;
  content: "";
  top: -51px;
  left: 3px;
  background-image: url("../../img/about-us-4.svg");
  width: 35px;
  height: 34px;
}
.about-us__item:nth-child(5)::before {
  position: absolute;
  content: "";
  top: -51px;
  left: -6px;
  background-image: url("../../img/about-us-5.svg");
  width: 55px;
  height: 23px;
}
.about-us__item:last-child::before {
  position: absolute;
  content: "";
  top: -54px;
  left: 3px;
  background-image: url("../../img/about-us-6.svg");
  background-repeat: no-repeat;
  width: 33px;
  height: 38px;
}
.button {
  box-sizing: border-box;
  padding: 16px 0;
  height: 52px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 20px;
  border: 2px solid #444444;
  background-color: #fff;
}
.button--mobile {
  width: 260px;
}
.button:hover {
  background-color: #444444;
  color: #fff;
}
.button:focus .button__text {
  opacity: 0.3;
}
.copyright__link {
  display: flex;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: #63d1bb;
}
.copyright__link:hover {
  color: #46c1ae;
}
.copyright__link:active .copyright__icon {
  opacity: 0.3;
}
.copyright__text {
  display: block;
  margin-top: 25px;
  margin-bottom: 32px;
  padding-left: 39px;
  color: #000;
}
.copyright__icon {
  margin-left: 28px;
  margin-top: 17px;
}
.main-nav {
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
}
.main-nav__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.main-nav__item {
  height: 79px;
}
.main-nav__link {
  position: relative;
  display: block;
  padding: 29px 0 27px 76px;
  border-top: 1px solid #ececec;
}
.main-nav__link:hover .main-nav__text {
  opacity: 0.6;
}
.main-nav__link:hover .main-nav__cart,
.main-nav__link:hover .main-nav__loop {
  color: #63d1bb;
  fill: #63d1bb;
}
.main-nav__link:focus .main-nav__text,
.main-nav__link:focus .main-nav__cart,
.main-nav__link:focus .main-nav__loop {
  opacity: 0.3;
}
.main-nav__list--second-part .main-nav__item:last-child .main-nav__link {
  border-bottom: 1px solid #ececec;
}
.main-nav__cart,
.main-nav__loop {
  position: absolute;
  top: 32px;
  left: 32px;
  color: #cccccc;
  fill: #cccccc;
}
.page {
  height: 100%;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #fff;
}
.page-header__top {
  position: relative;
  height: 79px;
}
.page-header__logo {
  display: block;
  padding-left: 116px;
  padding-top: 22px;
  padding-bottom: 22px;
}
.page-header__button {
  position: absolute;
  width: 21px;
  height: 20px;
  top: 29px;
  right: 30px;
  border: none;
  outline: none;
  background-image: url("../../img/page-header-menu-close-icon.svg");
  background-repeat: no-repeat;
  background-color: transparent;
}
.page-footer {
  position: relative;
  font-family: "Open Sans", "Arial", sans-serif;
}
.page-footer__contacts {
  padding: 0 30px;
}
.page-footer__title {
  margin: 0;
  padding: 0;
  margin-top: 44px;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
}
.page-footer__contacts-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-top: 28px;
  font-size: 14px;
  line-height: 30px;
  color: #444444;
}
.page-footer__contacts-list:last-of-type {
  margin-top: 29px;
}
.page-footer__contacts-item:last-child {
  display: flex;
  flex-direction: row;
  margin-top: 30px;
}
.page-footer__contacts-email {
  position: relative;
  margin-left: 29px;
}
.page-footer__contacts-email::after {
  position: absolute;
  content: "";
  top: 21px;
  left: 0;
  width: 167px;
  height: 1px;
  background-color: #62d1ba;
}
.page-footer__contacts-email:hover {
  color: #62d1ba;
}
.page-footer__contacts-email:active::after {
  opacity: 0.1;
}
.page-footer__contacts-address {
  margin: 0;
  padding: 0;
  margin-left: 34px;
}
.page-footer__map {
  margin-top: 62px;
  width: 100%;
  height: 454px;
}
.page-footer-map-pin {
  position: absolute;
  top: 460px;
  left: 140px;
}
.page-footer__button-section {
  padding: 0 30px;
}
.page-footer__button {
  display: block;
  margin-top: 36px;
}
.page-footer__socials-copyright {
  background-color: #f1f1f1;
}
.page-footer__socials-wrapper {
  padding: 0 30px;
  margin-top: 39px;
  border-bottom: 1px solid #e5e5e5;
}
.page-footer__copyright-wrapper {
  padding: 0 30px;
}
.promo {
  height: 489px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  background-image: url("../../img/promo-background-image.jpg");
  background-repeat: no-repeat;
  background-position: 0 -20%;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../../img/promo-background-image@2x.jpg");
    background-size: 320px auto;
  }
}
.promo__title {
  margin: 0;
  padding: 0;
  padding: 0 30px;
  padding-top: 73px;
  text-align: center;
  font-size: 27px;
  line-height: 30px;
}
.promo__button {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 24px 36px 18px 30px;
  font-size: 19px;
  line-height: 24px;
  color: #fff;
}
.promo__button:first-of-type {
  margin-top: 120px;
  background-color: #63d1bb;
}
.promo__button:last-of-type {
  background-color: #56c3b2;
  padding-right: 27px;
}
.promo__button svg {
  opacity: 0.5;
}
.promo__button:hover:first-of-type {
  margin-top: 115px;
  padding-bottom: 23px;
}
.promo__button:hover:last-of-type {
  margin-top: -5px;
  padding-bottom: 23px;
}
.promo__button:hover svg {
  opacity: 0.7;
}
.promo__button:focus svg {
  opacity: 0.3;
}
.promo-text {
  width: 130px;
}
.product {
  position: relative;
  font-family: "Open Sans", "Arial", sans-serif;
}
.product::before {
  position: absolute;
  content: "";
  bottom: -6px;
  background-image: url("../../img/zigzag-line.svg");
  width: 100%;
  height: 6px;
}
.product__wrapper {
  padding: 0 30px;
}
.product__title {
  margin: 0;
  padding: 0;
  margin-top: 31px;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
}
.product__subtitle {
  margin: 0;
  padding: 0;
  margin-top: 4px;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: #63d1bb;
}
.product__text {
  margin: 0;
  padding: 0;
  margin-top: 18px;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
.product__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  height: 39px;
  font-size: 15px;
  line-height: 20px;
  border-bottom: 1px solid #ececec;
  color: #444444;
}
.product__list:first-of-type {
  margin-top: 18px;
}
.product__list .product__definition {
  margin-left: 30px;
  width: 162px;
  text-align: left;
}
.product__image {
  margin-top: 28px;
}
.product__price {
  margin: 0;
  padding: 0;
  margin-top: 25px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  text-align: center;
}
.product__button {
  margin-top: 22px;
  margin-bottom: 36px;
}
.reviews {
  position: relative;
  font-family: "Open Sans", "Arial", sans-serif;
}
.reviews::before {
  position: absolute;
  content: "";
  bottom: -6px;
  background-image: url("../../img/zigzag-line.svg");
  width: 100%;
  height: 6px;
}
.reviews__wrapper {
  padding: 0 30px;
}
.reviews__title {
  margin: 0;
  padding: 0;
  margin-top: 56px;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
}
.reviews__response {
  margin: 0;
  padding: 0;
}
.reviews__text {
  margin: 0;
  padding: 0;
  margin-top: 14px;
  font-size: 15px;
  line-height: 30px;
}
.reviews__author {
  display: block;
  margin-top: 34px;
  font-style: normal;
  font-size: 17px;
  line-height: 24px;
}
.reviews__mail {
  display: block;
  margin: 0;
  padding: 0;
  margin-top: 6px;
  font-style: normal;
  font-size: 17px;
  line-height: 24px;
}
.reviews__controls {
  display: flex;
  flex-direction: row;
  margin-top: 30px;
}
.reviews__controls-button {
  width: 130px;
  height: 52px;
  border: 1px solid #ececec;
  outline: none;
  background-color: #fff;
}
.reviews__arrow {
  position: relative;
  top: 3px;
  left: -2px;
}
.reviews__arrow:hover {
  opacity: 0.6;
}
.reviews__arrow:active {
  opacity: 0.3;
}
.reviews__button {
  margin-top: 40px;
  margin-bottom: 45px;
}
.socials {
  display: flex;
  align-items: center;
  justify-content: center;
}
.socials__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  padding: 25px 59px 27px 59px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 142px;
}
.socials-link {
  color: #000;
}
.socials-link:hover {
  color: #46c1ae;
}
.socials-link:focus {
  opacity: 0.3;
}
